.container {
  max-width: 317px;
  margin: 44px 117px;
  background-color: #1a1b21;
}

.info img {
  width: 320px;
  max-width: 100%;
}
.info {
  text-align: center;
}

.info h2 {
  font-weight: 700;
  font-size: 25px;
  color: #fff;
  margin-top: 21px;
}

.info h3 {
  margin-top: 5px;
  font-size: 13px;
  color: #f3bf99;
  font-weight: 400;
}

.info p {
  font-size: 10px;
  color: #f5f5f5;
  margin-top: 10px;
  font-weight: 400;
}

.info button {
  padding: 9px 13px 9px 11px;
  width: 247px;
  height: 34px;
  background-color: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  margin: 20px 35px;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}

.info button img {
  display: inline-block;
  height: 9px;
  width: 12px;
  color: #1e1f26;
  margin-right: 12.2px;
}

.about,
.interests {
  color: #dcdcdc;
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;
  padding: 0 35px;
}

.about p,
.interests p {
  font-weight: 400;
}

.interests {
  margin-top: 20px;
}

.footer {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  padding: 10px 25px;
  background-color: #161619;
}
